/* Invoice.css */
.invoice-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    margin-top: 10px;
    margin-bottom: 10px;
}

.invoice-title {
    text-align: center;
    color: #333;
}

label {
    display: block;
    margin-bottom: 10px;
}

input,
select {
    width: 100%;
    padding: 8px;
    margin-top: 5px;
    margin-bottom: 15px;
    box-sizing: border-box;
}

.add-button,
.generate-button,
.print-button {
    background-color: #4caf50;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-bottom: 15px;
}

.add-button:hover,
.generate-button:hover,
.print-button:hover {
    background-color: #45a049;
}

.papad-list {
    margin-top: 20px;
}

ul {
    list-style: none;
    padding: 0;
}

li {
    margin-bottom: 5px;
}

/* Additional Styles for the Invoice Content */
.invoice-content {
    font-family: 'Arial', sans-serif;
}

.invoice-content h2 {
    color: #4caf50;
}

.invoice-content p {
    margin-bottom: 10px;
}

.invoice-content h3 {
    color: #333;
    margin-top: 15px;
}

.invoice-content ul {
    padding-left: 20px;
}

.invoice-content li {
    margin-bottom: 5px;
}

.invoice-content .charges-section {
    margin-top: 20px;
}

.invoice-content .total-amount {
    margin-top: 15px;
    font-size: 18px;
    font-weight: bold;
    color: #4caf50;
}

@media (max-width: 768px) {
    /* Add responsive styles here */
    .invoice-container {
        max-width: 100%;
    }
}
